import { FormsModule } from '@angular/forms';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { BsDropdownModule } from 'ngx-bootstrap/dropdown';
import { ButtonsModule } from 'ngx-bootstrap/buttons';

import { ChartsModule } from 'ng2-charts';

import { AppComponent } from './app.component';
import { GraficoErroComponent } from './grafico-erro/grafico-erro.component';
import { GraficoRedeNeuralComponent } from './grafico-rede-neural/grafico-rede-neural.component';
import { HistogramaErroComponent } from './histograma-erro/histograma-erro.component';

@NgModule({
  declarations: [
    AppComponent,
    GraficoErroComponent,
    GraficoRedeNeuralComponent,
    HistogramaErroComponent
],
  imports: [
    BsDropdownModule.forRoot(),
    ButtonsModule.forRoot(),
    BrowserModule,
    ChartsModule,
    FormsModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



