import { AfterViewInit, Component, ElementRef, Input, OnInit, ViewChild } from '@angular/core';
import * as Chart from 'chart.js';

@Component({
  selector: 'histograma-erro',
  templateUrl: './histograma-erro.component.html',
  styleUrls: ['./histograma-erro.component.scss']
})
export class HistogramaErroComponent implements OnInit, AfterViewInit {

  @ViewChild('histogramaErro')
  private canvasElement: ElementRef;

  private scatter;

  @Input()
  private errors: {x:number,y:number}[];

  private config = {
    type: 'scatter',
    data: {
      datasets: [
        {
          label: 'Treinamento',
          data: [],
          pointBackgroundColor: '#ee0000',
          backgroundColor: '#ee4444',
          borderColor: '#990000',
          pointBorderColor: '#990000',
          showLine: true
        },
        {
          label: 'Teste',
          data: [],
          pointBackgroundColor: '#0000ee',
          backgroundColor: '#4444ee',
          borderColor: '#000099',
          pointBorderColor: '#000099',
          showLine: true
        }
      ]
    },
    options: {
      scales: {
        xAxes: [
          {
              type: 'linear',
              position: 'bottom'
          }
        ]
      },
      elements: {
        line: {
          tension: 0,
          fill: false
        }
      },
      showLines: true
    }
  };

  constructor(
  ) { }

  ngOnInit() {

  }

  ngAfterViewInit() {
    let ctx = this.canvasElement.nativeElement.getContext('2d');
    this.scatter = new Chart(this.canvasElement.nativeElement.getContext('2d'), this.config);
  }

  private calculaHistograma(erros: number[]): number[] {
    let passo = 0.1;
    let limiteAtual = 0;

    let histograma: any = {};
    histograma[limiteAtual.toString()] = 0;

    erros.forEach(erro => {
      while(erro >= limiteAtual) {
        limiteAtual += passo;
        histograma[limiteAtual.toString()] = 0;
      }
      histograma[limiteAtual.toString()] += 1;
    });

    limiteAtual += passo;
    histograma[limiteAtual.toString()] = 0;

    return histograma;
  }

  public atualizaHistograma(errosTreinamento: number[], errosTeste: number[]) {
    
    let histogramaTreinamento: any = this.calculaHistograma(errosTreinamento);
    let histogramaTeste: any = this.calculaHistograma(errosTeste);


    this.config.data.datasets[0].data = Object.keys(histogramaTreinamento)
      .map((key) => {
        return {
          y: histogramaTreinamento[key],
          x: parseFloat(key)
        };
      });

    this.config.data.datasets[1].data = Object.keys(histogramaTeste)
      .map((key) => {
        return {
          y: histogramaTeste[key],
          x: parseFloat(key)
        };
      });
    this.scatter.update();
  }
}



