import { HistogramaErroComponent } from './histograma-erro/histograma-erro.component';
import {
    Activations,
    adicionaRBF,
    backProp,
    buildNetwork,
    Errors,
    forwardProp,
    Node,
    RegularizationFunction,
    updateWeights,
} from './neural-network';
import { GraficoRedeNeuralComponent } from './grafico-rede-neural/grafico-rede-neural.component';
import { GraficoErroComponent } from './grafico-erro/grafico-erro.component';
import { AfterViewInit, Component, OnInit, ViewChild, SimpleChanges } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit, AfterViewInit {
  public new_nn: Node[][];

  pesoRBF: string;

  @ViewChild('graficoErro') graficoErro: GraficoErroComponent;
  @ViewChild('graficoRedeNeural') graficoRedeNeural: GraficoRedeNeuralComponent;
  @ViewChild('histogramaErro') histogramaErro: HistogramaErroComponent;

  public data = {
    train: [],
    test: []
  };

  public baseDeDados: "degrau" | "parabola" | "seno"  = "degrau";

  public errors: { x: number, y: number}[] = [];

  rbfAdicionada: boolean = false;

  public ngOnInit(): void {

  }

  public ngAfterViewInit(): void {
    this.initialize();
  }

  initialize() {
    this.data.train.splice(0, this.data.train.length);
    this.data.test.splice(0, this.data.test.length);

    for(let i=0; i<20; i++) {
      let x = Math.random()*2 - 1;
      let y;

      switch(this.baseDeDados) {
        case "degrau":
          y = (x>0?1:-1);
          break;
        case "parabola":
          y = -1 * ((x*x) + Math.random()*0.05 - 1);
          break;
        case "seno":
          y = Math.sin(x*Math.PI);
    
          if(i == 12) {
            x = 0.6;
            y = -1;
          }
          break;
      }

      if(i>6) {
        this.data.train.push({ x, y });
      } else {
        this.data.test.push({ x, y });
      }
    }

    // this.nn = new NeuralNetwork([
    //   // [
    //   //   new Neuron(1, Tgh)
    //   // ]
    //   [
    //     new Neuron(1, Tgh),
    //     new Neuron(1, Tgh),
    //     new Neuron(1, Tgh)
    //   ],
    //   [
    //     new Neuron(3, Tgh)
    //   ]
    // ]);

    this.new_nn = buildNetwork([1,3,1], Activations.TANH, Activations.LINEAR, undefined, ['1'], false);
    // this.new_nn = buildNetwork([1,5,1], Activations.RBF, Activations.LINEAR, undefined, ['1'], false);
    this.graficoRedeNeural.atualizaNN(this.new_nn);


    this.histogramaErro.atualizaHistograma([], []);
    this.graficoErro.clear();
    this.rbfAdicionada = false;
    this.epocasTreinadas = 0;
  }

  public epocasTreinadas = 0;
  public numEpocas=1;
  public treinarEpoca() {
    for(let i=0; i<this.numEpocas; i++) {
      this.data.train.forEach((point, i) => {
        let input = [point.x];
        forwardProp(this.new_nn, input);
        backProp(this.new_nn, point.y, Errors.SQUARE);
      });
      updateWeights(this.new_nn, 0.5, 0);
      // Compute the loss.
      let lossTrain = this.getLoss(this.new_nn, this.data.train);
      let lossTest = this.getLoss(this.new_nn, this.data.test);
      // let erro = this.nn.trainEpoch(this.data.train.map((tuple) => {
      //   return {
      //     input: [tuple.x],
      //     output: [tuple.y]
      //   }
      // }));

      this.graficoErro.adicionaErro(lossTrain, lossTest);
      this.graficoRedeNeural.atualizaNN(this.new_nn);
    }

    let histogramaTreinamento = this.getLossHistogram(this.new_nn, this.data.train);
    let histogramaTeste = this.getLossHistogram(this.new_nn, this.data.test);
    this.histogramaErro.atualizaHistograma(histogramaTreinamento, histogramaTeste);

    this.epocasTreinadas += this.numEpocas;
  }

  adicionarRBF() {
    adicionaRBF(this.new_nn, parseFloat(this.pesoRBF));
    this.graficoRedeNeural.atualizaNN(this.new_nn);
    this.rbfAdicionada = true;
  }

  getLoss(network: Node[][], dataPoints: {x: number, y: number}[]): number {
    let loss = 0;
    for (let i = 0; i < dataPoints.length; i++) {
      let dataPoint = dataPoints[i];
      let input = [dataPoint.x];
      let output = forwardProp(network, input);
      loss += Errors.SQUARE.error(output, dataPoint.y);
    }
    return loss / dataPoints.length;
  }

  getLossHistogram(network: Node[][], dataPoints: {x: number, y: number}[]): number[] {
    let losses:number[] = [];
    for (let i = 0; i < dataPoints.length; i++) {
      let dataPoint = dataPoints[i];
      let input = [dataPoint.x];
      let output = forwardProp(network, input);
      losses.push(Errors.SQUARE.error(output, dataPoint.y));
    }
    return losses.sort();
  }
}



