import { AfterViewInit, Component, ElementRef, Input, OnInit, ViewChild, SimpleChanges } from '@angular/core';
import * as Chart from 'chart.js';

@Component({
  selector: 'grafico-erro',
  templateUrl: './grafico-erro.component.html',
  styleUrls: ['./grafico-erro.component.scss']
})
export class GraficoErroComponent implements OnInit, AfterViewInit {

  @ViewChild('graficoErro')
  private canvasElement: ElementRef;

  private scatter;

  @Input()
  private errors: {x:number,y:number}[];

  private config = {
    type: 'scatter',
    data: {
      datasets: [
        {
          label: 'Erro Treinamento',
          data: [],
          pointBackgroundColor: '#ee0000',
          backgroundColor: '#ee4444',
          borderColor: '#990000',
          pointBorderColor: '#990000',
          showLine: true
        },
        {
            label: 'Teste',
            data: [],
            pointBackgroundColor: '#00ee00',
            backgroundColor: '#44ee44',
            borderColor: '#009900',
            pointBorderColor: '#009900'
        },
        // {
        //     label: 'Scatter Line Dataset',
        //     data: [
        //         {
        //             x: -10,
        //             y: 5
        //         }, {
        //             x: 0,
        //             y: 0
        //         }, {
        //             x: 10,
        //             y: 10
        //         }
        //     ],
        //     pointBackgroundColor: '#0000ee',
        //     backgroundColor: '#4444ee',
        //     borderColor: '#000099',
        //     pointBorderColor: '#000099',
        //     showLine: true,
        //     pointRadius: 0,
        //     pointHoverRadius: 0,
        //     lineTension: 0.2
        // }
      ]
    },
    options: {
      scales: {
        xAxes: [
          {
              type: 'linear',
              position: 'bottom'
          }
        ]
      },
      elements: {
        line: {
          tension: 0,
          fill: false
        }
      },
      showLines: true
    }
  };

  constructor(
  ) { }

  ngOnInit() {

  }

  ngAfterViewInit() {
    let ctx = this.canvasElement.nativeElement.getContext('2d');
    this.scatter = new Chart(this.canvasElement.nativeElement.getContext('2d'), this.config);
  }

  public clear() {
    this.config.data.datasets[0].data = [];
    this.config.data.datasets[1].data = [];
    this.scatter.update();
  }

  public adicionaErro(erroTreinamento: number, erroTeste: number) {
    this.config.data.datasets[0].data.push({
      y: erroTreinamento,
      x: this.config.data.datasets[0].data.length
    });
    this.config.data.datasets[1].data.push({
      y: erroTeste,
      x: this.config.data.datasets[1].data.length
    });
    this.scatter.update();
  }
}



