  // public atualizaNN(nn: NeuralNetwork) {
  //   let saidaNN = [];
  //   for(let i = -1; i<=1; i+=0.05) {
  //     let out = nn.out([i]);
  //     saidaNN.push({
  //       x: i,
  //       y: out[0]
  //     });
  //   }
  //   this.config.data.datasets[0].data = saidaNN;
  //   this.scatter.update();
